.App {
  text-align: center;
}



.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.flip-card {
  background-color: transparent;
  perspective: 1000px;
  margin:5% 5% 0;
  background-color: #222;
  height:270px;
  width:30%;
  border-radius:10%;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-radius:10%;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  border-radius:10%;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.flip-card-front img{
  width:99%;
  height:99%;
  border-radius:10%;
}

.flip-card-back {
  background-color: #191b25;
  color: white;
  transform: rotateY(180deg);
  text-align: center;
  word-wrap: break-word;
  line-height: 50px;
}


@media screen and (max-width: 480px) {

.flip-card{
    width: 100%;
}
}
@media screen
  and (min-width: 400px)
  and (max-width: 1200px) {
      .flip-card-back{
        line-height: 25px;
        padding:20px;
      }
  }